        prop="owner"
        label="栏目选择">
        <el-select
          style="width:900px"
          v-model="form.owner"
          filterable
          allow-create
          placeholder="请选择标签">
          <el-option
            v-for="item in userList"
            :key="item.userId"
            :label="item.username"
            :value="item.username">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="标题"
        prop="name">
        <el-input v-model.number="form.name" style="width:900px"></el-input>
      </el-form-item>
      <el-form-item
        prop="validCertificate"
        label="首页图片">
        <el-upload
          action=""
          list-type="picture-card"
          :file-list="fileList"
          :on-preview="handlePictureCardPreview"
          :auto-upload="false"
          :on-remove="handleRemove"
          :on-change="show"
          :limit="1"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="logVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <el-form-item
        label="发布内容"
      >
        <button size="primary" type="info" icon="plus" @click="getContent">获取内容</button>
        <UEditor :config=config ref="ueditor" style="width:900px"></UEditor>
      </el-form-item>
      <el-form-item>
        <el-button @click="initCompany">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>

