          //     // 'splittorows', //拆分成行
          //     // 'splittocols', //拆分成列
          //     // 'splittocells', //完全拆分单元格
          //     // 'deletecaption', //删除表格标题
          //     // 'inserttitle', //插入标题
          //     // 'mergecells', //合并多个单元格
          //     // 'deletetable', //删除表格
          //     // 'cleardoc', //清空文档
          //     // 'insertparagraphbeforetable', //"表格前插入行"
          //     // 'insertcode', //代码语言
          //     // 'fontfamily', //字体
          //     // 'fontsize', //字号
          //     // 'paragraph', //段落格式
          //     // 'simpleupload', //单图上传
          //     // 'insertimage', //多图上传
